    padding-top: 6rem;
    padding-bottom: 3rem;
    background: linear-gradient(rgba(255, 255, 255, 1), rgba(197, 234, 249, 1));
    text-align: center;
}

.footer a {
    text-decoration: none;
}

.footer .fa-stack {
    width: 2em;
    margin-bottom: 1.25rem;
    margin-right: 0.375rem;
    font-size: 1.5rem;
}

.footer .fa-stack .fa-stack-1x {
    color: #252c38;
    transition: all 0.2s ease;
}

.footer .fa-stack .fa-stack-2x {
    color: #ffffff;
    transition: all 0.2s ease;
}

.footer .fa-stack:hover .fa-stack-1x {
    color: #ffffff;
}

.footer .fa-stack:hover .fa-stack-2x {
    color: #252c38;
}

/*********************/
/*     Copyright     */
/*********************/
.copyright {
    padding-top: 1.5rem;
    background-color: #c5eaf9;
    text-align: center;
}

/******************************/
/*     Back To Top Button     */
/******************************/
a.back-to-top {
    position: fixed;
    z-index: 999;
    right: 12px;
    bottom: 12px;
    display: none;
    width: 42px;
    height: 42px;
    border-radius: 30px;
    background: #343e48 url("./images/up-arrow.png") no-repeat center 47%;
    background-size: 18px 18px;
    text-indent: -9999px;
}

a:hover.back-to-top {
    background-color: #000000;
}

/***********************/
/*     Extra Pages     */
/***********************/
.ex-header {
    padding-top: 8.5rem;
    padding-bottom: 4rem;
    background-color: #f1f9fc;
}

.ex-basic-1 .list-unstyled .fas {
    font-size: 0.375rem;
    line-height: 1.625rem;
}

.ex-basic-1 .text-box {
    padding: 1.25rem 1.25rem 0.5rem 1.25rem;
    background-color: #f1f9fc;
}

.ex-cards-1 .card {
    border: none;
    background-color: transparent;
}

.ex-cards-1 .card .fa-stack {
    width: 2em;
    font-size: 1.125rem;
}

.ex-cards-1 .card .fa-stack-2x {
    color: #ff6e84;
}

.ex-cards-1 .card .fa-stack-1x {
    color: #ffffff;
    font-weight: 700;
    line-height: 2.125rem;
}

/*************************/
/*     Media Queries     */
/*************************/

/* Min-width 768px */
@media (min-width: 768px) {
    /* Extra Pages */
    .ex-basic-1 .text-box {
        padding: 1.75rem 2rem 0.875rem 2rem;
    }
    /* end of extra pages */
}
/* end of min-width 768px */

/* Min-width 1024px */
@media (min-width: 1024px) {
    /* General Styles */

    .btn-solid-lg {
        margin-right: 0.5rem;
        margin-left: 0;
    }

    /* end of general styles */

    /* Navigation */
    .navbar {
        background-color: transparent;
        flex-wrap: nowrap;
        justify-content: start;
        padding-left: 0;
        padding-right: 0;
        padding-top: 1.75rem;
    }

    .navbar-collapse {
        flex-basis: auto;
    }

    .navbar.top-nav-collapse {
        padding-top: 0.5rem;
        padding-bottom: 0.5rem;
        background-color: #f1f9fc;
    }

    .offcanvas-collapse {
        position: static;
        top: auto;
        bottom: auto;
        left: auto;
        width: auto;
        padding-right: 0;
        padding-left: 0;
        background-color: transparent;
        overflow-y: visible;
        visibility: visible;
    }

    .offcanvas-collapse.open {
        -webkit-transform: none;
        transform: none;
    }

    .nav-link {
        padding-right: 0.625rem;
        padding-left: 0.625rem;
    }

    .dropdown-menu {
        position: absolute;
        margin-top: 0.25rem;
        box-shadow: 0 3px 3px 1px rgba(0, 0, 0, 0.05);
    }

    .dropdown-divider {
        width: 90%;
    }

    /* end of navigation */

    /* Details Lightbox */
    .lightbox-basic {
        padding: 3rem 3rem;
    }
    /* end of details lightbox */

    /* Features */
    .cards-1 .card {
        display: inline-block;
        width: 306px;
        vertical-align: top;
    }

    .cards-1 .card:nth-of-type(3n + 2) {
        margin-right: 1rem;
        margin-left: 1rem;
    }
    /* end of features */

    /* Statistics */
    .counter {
        padding-top: 5rem;
    }
    /* end of statistics */

    /* Testimonials */
    .slider-1 .swiper-container {
        width: 92%;
    }

    .slider-1 .swiper-button-prev {
        left: -16px;
        width: 22px;
        background-size: 22px 34px;
    }

    .slider-1 .swiper-button-next {
        right: -16px;
        width: 22px;
        background-size: 22px 34px;
    }
    /* end of testimonials */

    /* Pricing */
    .cards-2 .card {
        display: inline-block;
        max-width: 100%;
        width: 312px;
        vertical-align: top;
    }

    .cards-2 .card:nth-of-type(3n + 2) {
        margin-right: 0.375rem;
        margin-left: 0.375rem;
    }
    /* end of pricing */

    /* Conclusion */
    .basic-5 {
        text-align: left;
    }
    /* end of conclusion */

    /* Copyright */
    .copyright {
        text-align: left;
    }

    .copyright .list-unstyled li {
        display: inline-block;
        margin-right: 1rem;
    }

    .copyright .statement {
        text-align: right;
    }
    /* end of copyright */

    /* Extra Pages */
    .ex-cards-1 .card {
        display: inline-block;
        width: 306px;
        vertical-align: top;
    }

    .ex-cards-1 .card:nth-of-type(3n + 2) {
        margin-right: 1rem;
        margin-left: 1rem;
    }
    /* end of extra pages */
}
/* end of min-width 1024px */

/* Min-width 1280px */
@media (min-width: 1280px) {
    /* General Styles */
    .h1-large {
        font-size: 3.125rem;
        line-height: 3.75rem;
    }

    .container {
        max-width: 72rem;
    }
    /* end of general styles */

    /* Features */
    .cards-1 .card {
        width: 342px;
        padding-right: 2.875rem;
        padding-left: 2.875rem;
    }

    .cards-1 .card:nth-of-type(3n + 2) {
        margin-right: 1.5rem;
        margin-left: 1.5rem;
    }
    /* end of features */

    /* Statistics */
    .counter #counter .cell {
        margin-right: 2.5rem;
        margin-left: 2.5rem;
    }

    .counter #counter .counter-value {
        font-size: 3.75rem;
        line-height: 4.25rem;
    }
    /* end of statistics */

    /* Pricing */
    .cards-2 .card {
        width: 335px;
    }

    .cards-2 .card:nth-of-type(3n + 2) {
        margin-right: 2.25rem;
        margin-left: 2.25rem;
    }

    .cards-2 .card .card-body {
        padding-right: 2.25rem;
        padding-left: 2.25rem;
    }
    /* end of pricing */

    /* Extra Pages */
    .ex-cards-1 .card {
        width: 328px;
    }

    .ex-cards-1 .card:nth-of-type(3n + 2) {
        margin-right: 2.875rem;
        margin-left: 2.875rem;
    }
    /* end of extra pages */
}
/* end of min-width 1200px */


